/* eslint-disable no-undef */
import React, { Component } from "react";

import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { InputDate, ReanderSelect } from "../../../../components/helpers/field";
import {
  formatGram,
  getTglSystem,
} from "../../../../components/helpers/function";
import Tabel from "../../../../components/helpers/tabel";
import ValidasiTanggalLaporan from "../../../../Validasi/ValidasiTanggalLaporan";
// import LaporanBatalServiceExel from "./exel/LaporanBatalServiceExel";
import ExportTransaksiPenjualan from "./pdf/ExportTransaksiPenjualan";
import Loading from "react-fullscreen-loading";
import { GetDataGroup, getDataMarketplace } from "../../../../actions/datamaster_action";

class HeadFormLaporanPenjualanBarang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      date: new Date(),
      type_laporan: "SEMUA",
      databarang: [],
    };
    // this.handleSubmit = this.handleSubmit.bind(this);
    this.setStartDate = this.setStartDate.bind(this);
    this.setLastDate = this.setLastDate.bind(this);
  }

  componentDidMount() {
    this.props.change("type", "SEMUA");
    this.props.change("type_transaksi", "SEMUA");
    this.props.change("marketplace", "SEMUA");
    this.props.change("kode_group", "ALL");
    this.props.change("jenis_group", "ALL");
    this.props.dispatch(GetDataGroup());
    this.props.dispatch(getDataMarketplace());


  }

  exportPdf() {
    ExportTransaksiPenjualan(
      this.props.LaporanDataPenjualan,
      this.state.type_laporan
    );
  }
  setStartDate(date) {
    this.setState({
      tgl_awal: new Date(date),
    });
  }
  setLastDate(date) {
    this.setState({
      tgl_akhir: new Date(date),
    });
  }

  typeLaporan(e) {
    if (e === "SEMUA") {
      this.setState({
        type_laporan: "SEMUA",
      });
    } else {
      this.setState({
        type_laporan: "SELISIH",
      });
    }
  }

  render() {
    let marketplace = [
      {
        value: "SEMUA",
        name: "SEMUA",
      }
    ];
    this.props.getDataMarketplace.forEach(list => {
      let data = {
        value: list.kode_marketplace,
        name: list.kode_marketplace + " - " + list.nama_marketplace,
      };
      marketplace.push(data)
    });

    let databarang = [];
    let berat = 0;
    let berat_selisih = 0;
    let ongkos = 0;
    let harga_atribut = 0;
    let harga_jual = 0;
    let harga_total = 0;
    let harga_gram = 0;
    this.props.LaporanDataPenjualan.forEach((element) => {
      element.detail.forEach((detail) => {
        databarang.push(detail);
        berat = berat + detail.berat;
        berat_selisih = berat_selisih + detail.berat_selisih;
        harga_atribut = harga_atribut + detail.harga_atribut;
        ongkos = ongkos + detail.ongkos;
        harga_jual = harga_jual + detail.harga_jual;
        harga_total = harga_total + detail.harga_total;
        harga_gram = harga_gram + detail.harga_gram;
      });
    });

    let datagroup = [
      {
        value : "ALL",
        name : "SEMUA"
      }
    ]
    this.props.GetDataGroup.forEach(list => {
      let row = {
        name : list.kode_group,
        value : list.kode_group,
      }
      datagroup.push(row) 
    });
     
    const columns = [
      {
        dataField: "no_faktur_jual",
        text: "No FJ",
        footerAttrs: {
          colSpan: "2",
        },
        footer: (cell, row, num, index) => {
          return <div>Total Barang : {databarang.length} </div>;
        },
      },
      // {
      //   dataField: "kode_member",
      //   text: "Kode Member",
      // },
      {
        dataField: "nama_customer",
        text: "Nama Member",
      },
      // {
      //   dataField: "kode_barcode",
      //   text: "Kode Barcode",
      // },
      // {
      //   dataField: "nama_barang",
      //   text: "Nama Barang",
      // },
      {
        dataField: "berat",
        text: "Berat",
        headerClasses: "text-right",
        footer: (cell, row, num, index) => {
          return <div className="text-right">{formatGram(berat, 3)} </div>;
        },
        formatter: (cell) => {
          return <div className="text-right">{formatGram(cell, 3)}</div>;
        },
      },
      // {
      //   dataField: "berat_selisih",
      //   text: "Berat Selisih",
      //   headerClasses: 'text-right',
      //   footer: (cell, row, num, index) => {
      //     return <div className="text-right">{formatGram(berat_selisih, 3)} </div>;
      //   },
      //   formatter: (cell) => {
      //     return <div className="text-right">{formatGram(cell, 3)}</div>;
      //   },
      // },
      {
        dataField: "harga_jual",
        text: "Harga Jual",
        headerClasses: "text-right",
        footer: (cell, row, num, index) => {
          return (
            <div className="text-right">
              {harga_jual.toLocaleString("kr-KO")}{" "}
            </div>
          );
        },
        formatter: (cell) => {
          return (
            <div className="text-right">{cell.toLocaleString("kr-KO")}</div>
          );
        },
      },
      {
        dataField: "harga_atribut",
        text: "Harga Atribut",
        headerClasses: "text-right",
        footer: (cell, row, num, index) => {
          return (
            <div className="text-right">
              {harga_atribut.toLocaleString("kr-KO")}{" "}
            </div>
          );
        },
        formatter: (cell) => {
          return (
            <div className="text-right">{cell.toLocaleString("kr-KO")}</div>
          );
        },
      },

      {
        dataField: "ongkos",
        text: "Ongkos",
        headerClasses: "text-right",
        footer: (cell, row, num, index) => {
          return (
            <div className="text-right">{ongkos.toLocaleString("kr-KO")} </div>
          );
        },
        formatter: (cell) => {
          // return formatGram(cell,3);
          return (
            <div className="text-right">{cell.toLocaleString("kr-KO")}</div>
          );
        },
      },
      // {
      //   dataField: "ongkos",
      //   text: "Ongkos",
      //   headerClasses: 'text-right',
      //   footer: (cell, row, num, index) => {
      //     return <div className="text-right">{ongkos.toLocaleString('kr-KO')} </div>;
      //   },
      //   formatter: (cell) => {
      //     return <div className="text-right">{cell.toLocaleString('kr-KO')}</div>;
      //   },
      // },
     
      // {
      //   dataField: "harga_gram",
      //   text: "Harga/Gram",
      //   headerClasses: 'text-right',
      //   footer: (cell, row, num, index) => {
      //     return <div className="text-right">{harga_gram.toLocaleString('kr-KO')} </div>;
      //   },
      //   formatter: (cell) => {
      //     return <div className="text-right">{cell.toLocaleString('kr-KO')}</div>;
      //   },
      // },
      {
        dataField: "harga_total",
        text: "Harga Total",
        headerClasses: "text-right",
        footer: (cell, row, num, index) => {
          return (
            <div className="text-right">
              {harga_total.toLocaleString("kr-KO")}{" "}
            </div>
          );
        },
        formatter: (cell) => {
          return (
            <div className="text-right">{cell.toLocaleString("kr-KO")}</div>
          );
        },
      },
    ];
    const columnsSelisih = [
      {
        dataField: "no_faktur_jual",
        text: "No FJ",
        footerAttrs: {
          colSpan: "2",
        },
        footer: (cell, row, num, index) => {
          return <div>Total Barang : {databarang.length} </div>;
        },
      },
      // {
      //   dataField: "kode_member",
      //   text: "Kode Member",
      // },
      {
        dataField: "nama_customer",
        text: "Nama Member",
      },
    
      {
        dataField: "berat",
        text: "Berat",
        headerClasses: "text-right",
        footer: (cell, row, num, index) => {
          return <div className="text-right">{formatGram(berat, 3)} </div>;
        },
        formatter: (cell) => {
          return <div className="text-right">{formatGram(cell, 3)}</div>;
        },
      },
      {
        dataField: "berat_selisih",
        text: "Berat Selisih",
        headerClasses: "text-right",
        footer: (cell, row, num, index) => {
          return (
            <div className="text-right">{formatGram(berat_selisih, 3)} </div>
          );
        },
        formatter: (cell) => {
          return <div className="text-right">{formatGram(cell, 3)}</div>;
        },
      },
      {
        dataField: "harga_jual",
        text: "Harga Jual",
        headerClasses: "text-right",
        footer: (cell, row, num, index) => {
          return (
            <div className="text-right">
              {harga_jual.toLocaleString("kr-KO")}{" "}
            </div>
          );
        },
        formatter: (cell) => {
          return (
            <div className="text-right">{cell.toLocaleString("kr-KO")}</div>
          );
        },
      },
      {
        dataField: "harga_atribut",
        text: "Harga Atribut",
        headerClasses: "text-right",
        footer: (cell, row, num, index) => {
          return (
            <div className="text-right">
              {harga_atribut.toLocaleString("kr-KO")}{" "}
            </div>
          );
        },
        formatter: (cell) => {
          return (
            <div className="text-right">{cell.toLocaleString("kr-KO")}</div>
          );
        },
      },
     
      {
        dataField: "ongkos",
        text: "Ongkos",
        headerClasses: "text-right",
        footer: (cell, row, num, index) => {
          return (
            <div className="text-right">{ongkos.toLocaleString("kr-KO")} </div>
          );
        },
        formatter: (cell) => {
          // return formatGram(cell,3);
          return (
            <div className="text-right">{cell.toLocaleString("kr-KO")}</div>
          );
        },
      },
      // {
      //   dataField: "ongkos",
      //   text: "Ongkos",
      //   headerClasses: 'text-right',
      //   footer: (cell, row, num, index) => {
      //     return <div className="text-right">{ongkos.toLocaleString('kr-KO')} </div>;
      //   },
      //   formatter: (cell) => {
      //     return <div className="text-right">{cell.toLocaleString('kr-KO')}</div>;
      //   },
      // },
    
      // {
      //   dataField: "harga_gram",
      //   text: "Harga/Gram",
      //   headerClasses: 'text-right',
      //   footer: (cell, row, num, index) => {
      //     return <div className="text-right">{harga_gram.toLocaleString('kr-KO')} </div>;
      //   },
      //   formatter: (cell) => {
      //     return <div className="text-right">{cell.toLocaleString('kr-KO')}</div>;
      //   },
      // },
      {
        dataField: "harga_total",
        text: "Harga Total",
        headerClasses: "text-right",
        footer: (cell, row, num, index) => {
          return (
            <div className="text-right">
              {harga_total.toLocaleString("kr-KO")}{" "}
            </div>
          );
        },
        formatter: (cell) => {
          return (
            <div className="text-right">{cell.toLocaleString("kr-KO")}</div>
          );
        },
      },
      // {
      //   dataField: "kode_marketplace",
      //   text: "Marketplace",
      // },
    ];
    const expandRow = {
      renderer: (row) => (
        <div className="container">
          <div className="row">
            <div className="col-12">
              Berat Awal : {formatGram(row.berat_awal, 3)} <br />
              Berat Selisih : {formatGram(row.berat_selisih, 3)} <br />
              Nama Atribut : {row.nama_atribut} <br />
              Berat Atribut : {row.berat_atribut} <br />
              Harga Atribut : {row.harga_atribut.toLocaleString("kr-KO")} <br />
              Ongkos : {row.ongkos.toLocaleString("kr-KO")} <br />
              Harga/Gram : {row.harga_gram.toLocaleString("kr-KO")} <br />
              Nama Member : {row.nama_customer} <br />
            </div>
          </div>
        </div>
      ),
      showExpandColumn: true,
      expandByColumnOnly: true,
    };
    return (
      <form onSubmit={this.props.handleSubmit} autoComplete="off">
        <div className="row">
          <div className="col-lg-4">
            <Field
              name="tgl_awal"
              component={InputDate}
              label="Tanggal Dari"
              type="text"
              selected={this.state.tgl_awal}
              onChange={(date) => this.setStartDate(date)}
              placeholder="Masukan Tanggal Dari"
              onFocus={() => setTimeout(() => {
                HideKeyboard.postMessage("")
              }, 100)}

            />
          </div>

          <div className="col-lg-4">
            <Field
              name="tgl_akhir"
              component={InputDate}
              type="text"
              selected={this.state.tgl_akhir}
              onChange={(date) => this.setLastDate(date)}
              label="Tanggal Akhir"
              placeholder="Masukan Tanggal Akhir"
              onFocus={() => setTimeout(() => {
                HideKeyboard.postMessage("")
              }, 100)}

            />
          </div>
          <div className="col-lg-4">
            <Field
              name="type"
              component={ReanderSelect}
              onChange={(e) => this.typeLaporan(e)}
              options={[
                { value: "SEMUA", name: "SEMUA" },
                { value: "SELISIH", name: "SELISIH" },
              ]}
              label="Type Laporan"
              placeholder="Pilih Type"
            />
          </div>
          <div className="col-lg-4">
            <Field
              name="type_transaksi"
              component={ReanderSelect}
              options={[
                { value: "SEMUA", name: "SEMUA" },
                { value: "PENJUALAN", name: "PENJUALAN" },
                { value: "AMBILTITIPAN", name: "AMBIL TITIPAN" },
                { value: "AMBILPESANAN", name: "AMBIL PESANAN" },
              ]}
              label="Type Transaksi"
              placeholder="Pilih Type Transaksi"
            />
          </div>
          <div className="col-lg-3">
            <Field
              name="kode_group"
              component={ReanderSelect}
              options={datagroup}
              onChange={(e)=>this.setState({kode_group : e})}
              label="Kode Group"
              placeholder="Pilih Kode Group"
            />
          </div>
          <div className="col-lg-3">
            <Field
              name="jenis_group"
              component={ReanderSelect}
              options={
                [
                  {
                    value : "ALL",
                    name : "SEMUA",
                  },
                  {
                    value : "MUDA",
                    name : "MUDA",
                  },
                  {
                    value : "TUA",
                    name : "TUA",
                  }
                ]
              }
              label="Jenis Group"
              placeholder="Pilih Jenis Group"
            />
          </div>

          <div className="col-lg-2">
          <div className="form-group">
              <label>&nbsp;</label>
              <br/>
                  <button
                    type="submit"
                    value="PDF"
                    name="type_btn"
                    className="btn btn-primary btn-block"
                  >
                    {this.props.isLoading ? (
                      <>
                        <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                        Mencari
                      </>
                    ) : (
                      "Cari Laporan"
                    )}
                  </button>
                  </div>
          </div>
        

         
          <div className="col-12">
            <Tabel
              keyField="no_faktur_jual"
              data={this.props.export ? databarang : []}
              columns={
                this.state.type_laporan === "SELISIH" ? columnsSelisih : columns
              }
              empty={true}
              textEmpty="Data Laporan Transaksi Penjualan Kosong"
              expandRow={expandRow}
            />
          </div>
          <div className={this.props.export ? "col-lg-12" : "col-lg-12 d-none"}>
            {/* <label htmlFor="">&nbsp;</label> */}
            <div className="row">
              <div className="col-lg-12">
                <button
                  type="button"
                  onClick={() => this.exportPdf()}
                  className="btn btn-warning btn-block"
                >
                  {" "}
                  Export PDF{" "}
                </button>
              </div>
              {/* <div className="col-lg-6">
                <LaporanExelPenjualan
                  data={this.props.LaporanDataPenjualan}
                  type_laporan={this.state.type_laporan}
                />
              </div> */}
            </div>
          </div>
        </div>
        {this.props.isLoading ? (
          <Loading
            loading
            background="rgba(0, 0, 0, 0.35)"
            loaderColor="rgba(94, 147, 117, 1)"
          />
        ) : null}
      </form>
    );
  }
}

HeadFormLaporanPenjualanBarang = reduxForm({
  form: "HeadFormLaporanPenjualanBarang",
  enableReinitialize: true,
  validate: ValidasiTanggalLaporan,
})(HeadFormLaporanPenjualanBarang);
export default connect((state) => {
  return {
    getDataMarketplace: state.datamaster.getDataMarketplace,
    GetDataGroup: state.datamaster.GetDataGroup,
    initialValues: {
      tgl_awal: getTglSystem(),
      tgl_akhir: getTglSystem(),
    },
  };
})(HeadFormLaporanPenjualanBarang);
